@keyframes swim {

    0%,
    100% {
        transform: translateZ(0)
    }

    20% {
        transform: translate3d(0, -8px, 0) rotate(-13deg)
    }

    30% {
        transform: translate3d(0, -12px, 0) rotate(-13deg)
    }

    50% {
        transform: translate3d(0, -20px, 0) rotate(2.5deg)
    }

    70% {
        transform: translate3d(0, -10px, 0) rotate(18deg)
    }

    80% {
        transform: translate3d(0, -5px, 0) rotate(18deg)
    }

    90% {
        transform: translateZ(0) rotate(9eg)
    }
}

@keyframes wave {
    0% {
        transform: translateZ(0)
    }

    100% {
        transform: translate3d(-30px, 0, 0)
