.aurora-th {
    position: relative
}

.aurora-th .sortLoader {
    position: absolute;
    top: 50%;
    margin-top: -8px;
    margin-left: 5px;
    height: 15px;
    width: 15px
}

.aurora-th .headerText {
    transition: color .4s ease
}

.aurora-th .headerText+span {
    position: absolute;
    font-size: 10px;
    margin-top: 1px;
    margin-left: 7px;
    top: 50%;
    color: transparent;
    transition: color .25s ease;
    transform: translateY(-50%)
}

.aurora-th .headerText+span:hover {
    color: #dfdfdf
}

.aurora-th:hover .headerText+span {
    color: #999
}

.aurora-th.active .headerText {
    transition: color .3s ease;
    color: #0069ff
}

.aurora-th.active .headerText+span {
    color: #0069ff
}

.aurora-th.active .headerText:hover,
.aurora-th.active .headerText:hover+span {
    color: #0052ff
}

.aurora-region-button .regionButton {
    padding: 8px 6px;
    width: 83px;
    margin-bottom: 10px;
    float: left;
    margin-right: 3px;
    color: #676767;
    height: 32px;
    line-height: 1
}

.aurora-region-button .regionButton:focus {
    box-shadow: none
}

.aurora-region-button .regionButton[disabled] {
    color: #bbb
}

.aurora-region-button .regionButton__icon {
    border-radius: 2px;
    display: inline-block;
    float: left;
    margin-right: 5px
}

.aurora-region-button .regionButton__label {
    display: inline-block;
    font-size: 12px;
    float: left;
    padding: 2px 0 0;
    text-transform: uppercase
}

.aurora-region-button .regionButton__loader {
    float: left;
    display: inline-block;
    margin-right: 11px;
    margin-left: 7px;
    height: 12px;
    width: 12px
}

.form--inline .Button {
    width: auto;
    padding: 0 1rem
}

.form--inline .Button.Button--fullWidth {
    width: 100%;
    padding: 0
}

.aurora-show-more .Icon--arrowDown {
    margin: 4px 3px;
    font-size: 12px;
    color: #0069ff
}

.backups-drawer {
    height: 0;
    opacity: .1;
    pointer-events: none;
    overflow: hidden;
    position: relative;
    transition: height .25s ease-in-out, opacity .2s linear;
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    display: none
}

.backups-drawer.backups-drawer-is-open {
    z-index: 3
}

.backups-drawer.disabled {
    opacity: .5 !important;
    pointer-events: none !important
}

.backups-drawer .Resource {
    margin: 24px 0 0 73px
}

.backups-drawer .Resource::before {
    background-image: url(../img/sprites.png);
    background-size: 1140.5px 1121px;
    background-position: -994px -958.5px;
    width: 17.5px;
    height: 14px;
    position: absolute;
    left: -40px;
    margin-top: 15px
}

.backups-drawer__name {
    width: 745px;
    display: inline-block;
    vertical-align: top
}

.backups-drawer__action {
    position: absolute;
    margin-top: -25px;
    right: 4px
}

.statusIndicator {
    width: 10px;
    height: 10px;
    border: 1px solid #fff;
    border-radius: 100%;
    background: #dfdfdf;
    text-indent: -9999px;
    display: inline-block;
    vertical-align: middle;
    transform: scale(.999) translateZ(0);
    -webkit-backface-visibility: hidden;
    transition: background .3s ease
}

.statusIndicator.active,
.statusIndicator.online,
.statusIndicator.running {
    background: #15cd72
}

.statusIndicator.alert,
.statusIndicator.down,
.statusIndicator.error,
.statusIndicator.invalid {
    background: #ed4f32
}

.statusIndicator.degraded,
.statusIndicator.deleting,
.statusIndicator.failover,
.statusIndicator.resizing,
.statusIndicator.warning {
    background: #faee6c
}

.statusIndicator.new,
.statusIndicator.provisioning,
.statusIndicator.upgrading {
    background: #fff
}

.statusIndicator.hasBg {
    height: 8px;
    width: 8px;
    position: relative;
    border-width: 1px;
    z-index: 1;
    margin: 2px -2px
}

.statusIndicator+.status-spinner-bg {
    height: 10px;
    width: 10px;
    position: absolute;
    background: #fff;
    margin: -12px 23px;
    border-radius: 100%
}

