.FloatLabelSelect.select-is-required .validate-asterisk::after {
    content: '*';
    position: absolute;
    top: 11px;
    right: 11px;
    color: #0069ff;
    font-size: 25px;
    pointer-events: none;
    transition: all .3s ease
