.evs__icons {
  display: flex;
  align-items: center;
  padding: 20px 10px;
}

.evs__icons > span {
  padding: 5px 10px;
  margin: 5px;
  border-radius: 35px;
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  color: rgb(183 97 23);
  box-shadow: 0 0 2px rgb(183 97 23) inset;
  cursor: pointer;
}

.evs__info img {
  height: 20px;
}

span.vote_cover {
  background: #555;
  color: #fff;
  font-size: 15px;
  box-shadow: none;
  padding: 5px 25px;
}

.monitor {
  padding: 8px !important;
  background: hsl(210, 20%, 98%);
  box-shadow: 2px -4px 3px #ccc inset !important;
  border: 2px solid rgb(183 97 23);
}

.monitor_icon {
  height: 20px;
  width: 20px;
}

.evs__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px 15px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 10px;
  box-shadow: 0 0 1px rgb(183 97 23) inset;
  background: hsl(210, 20%, 98%);
  color: rgb(183 97 23);
}

.evs__info b {
  display: inline-block;
  width: 60px;
  font-weight: 600;
  color: rgb(183 97 23);
}

.trials {
  padding: 15px;
}

.trials h3 {
  padding: 2px 10px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.3em;
  text-align: center;
  color: #fff;
  background-color: rgb(183 97 23);
}

.trials p {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  padding: 10px;
  font-size: 0.6em;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #555;
  border: thin solid #555;
  border-radius: 5px;
}
